import React, { useState, useEffect } from 'react';
import { useNavigate } from 'react-router-dom';
import { Form, Button } from 'react-bootstrap';
import { useDispatch, useSelector } from 'react-redux';
import FormContainer from '../components/FormContainer';
import CheckoutSteps from '../components/CheckoutSteps';

import { saveShippingAddress } from '../actions/cartActions';

const ShippingScreen = () => {
    const navigate = useNavigate()
    
    const cart = useSelector(state => state.cart)
    const { shippingAddress } = cart

    const userLogin = useSelector(state => state.userLogin)
    const { userInfo } = userLogin


    const [address, setAddress] = useState(shippingAddress.address)
    const [city, setCity] = useState(shippingAddress.city)
    const [postalCode, setPostalCode] = useState(shippingAddress.postalCode)
    const [country, setCountry] = useState(shippingAddress.country)

    const dispatch = useDispatch()

    useEffect(() => {
        if (!userInfo) {
            navigate('/login?redirect=shipping')
        }
    }, [navigate, userInfo])

    const submitHandler = (e) => {
        e.preventDefault()
        dispatch(saveShippingAddress({ address, city, postalCode, country }))
        navigate('/payment')
    }

    return (
        <FormContainer>
            <CheckoutSteps step1 step2/>
            <div style={{
                backgroundColor: 'var(--bg-secondary)',
                borderRadius: '12px',
                padding: '2rem',
                border: '1px solid var(--border-color)'
            }}>
                <h1 style={{
                    color: 'var(--text-primary)',
                    fontSize: '1.75rem',
                    fontWeight: '600',
                    marginBottom: '0.5rem'
                }}>
                    Shipping Address
                </h1>
                <p style={{
                    color: 'var(--text-secondary)',
                    fontSize: '0.95rem',
                    marginBottom: '2rem'
                }}>
                    Enter your shipping details
                </p>
                
                <Form onSubmit={submitHandler}>
                    <Form.Group controlId='address' style={{ marginBottom: '1.5rem' }}>
                        <Form.Label style={{
                            color: 'var(--text-primary)',
                            fontWeight: '500',
                            marginBottom: '0.5rem'
                        }}>
                            Address
                        </Form.Label>
                        <Form.Control
                            type='text'
                            placeholder='Enter Address'
                            value={address}
                            required
                            onChange={(e) => setAddress(e.target.value)}
                            style={{
                                backgroundColor: 'var(--bg-tertiary)',
                                border: '1px solid var(--border-color)',
                                borderRadius: '8px',
                                color: 'var(--text-primary)',
                                padding: '0.75rem',
                                fontSize: '1rem'
                            }}
                        />
                    </Form.Group>

                    <Form.Group controlId='city' style={{ marginBottom: '1.5rem' }}>
                        <Form.Label style={{
                            color: 'var(--text-primary)',
                            fontWeight: '500',
                            marginBottom: '0.5rem'
                        }}>
                            City
                        </Form.Label>
                        <Form.Control
                            type='text'
                            placeholder='Enter City'
                            value={city}
                            required
                            onChange={(e) => setCity(e.target.value)}
                            style={{
                                backgroundColor: 'var(--bg-tertiary)',
                                border: '1px solid var(--border-color)',
                                borderRadius: '8px',
                                color: 'var(--text-primary)',
                                padding: '0.75rem',
                                fontSize: '1rem'
                            }}
                        />
                    </Form.Group>

                    <Form.Group controlId='postalCode' style={{ marginBottom: '1.5rem' }}>
                        <Form.Label style={{
                            color: 'var(--text-primary)',
                            fontWeight: '500',
                            marginBottom: '0.5rem'
                        }}>
                            Postal Code
                        </Form.Label>
                        <Form.Control
                            type='text'
                            placeholder='Enter Postal Code'
                            value={postalCode}
                            required
                            onChange={(e) => setPostalCode(e.target.value)}
                            style={{
                                backgroundColor: 'var(--bg-tertiary)',
                                border: '1px solid var(--border-color)',
                                borderRadius: '8px',
                                color: 'var(--text-primary)',
                                padding: '0.75rem',
                                fontSize: '1rem'
                            }}
                        />
                    </Form.Group>

                    <Form.Group controlId='country' style={{ marginBottom: '2rem' }}>
                        <Form.Label style={{
                            color: 'var(--text-primary)',
                            fontWeight: '500',
                            marginBottom: '0.5rem'
                        }}>
                            Country
                        </Form.Label>
                        <Form.Control
                            type='text'
                            placeholder='Enter Country'
                            value={country}
                            required
                            onChange={(e) => setCountry(e.target.value)}
                            style={{
                                backgroundColor: 'var(--bg-tertiary)',
                                border: '1px solid var(--border-color)',
                                borderRadius: '8px',
                                color: 'var(--text-primary)',
                                padding: '0.75rem',
                                fontSize: '1rem'
                            }}
                        />
                    </Form.Group>

                    <Button
                        type='submit'
                        variant='primary'
                        style={{
                            backgroundColor: 'var(--accent-primary)',
                            border: 'none',
                            borderRadius: '8px',
                            color: '#ffffff',
                            padding: '0.75rem 2rem',
                            fontSize: '1rem',
                            fontWeight: '600',
                            width: '100%',
                            transition: 'all 0.3s ease'
                        }}
                        onMouseOver={(e) => {
                            e.currentTarget.style.transform = 'translateY(-2px)'
                            e.currentTarget.style.boxShadow = '0 8px 16px rgba(0, 113, 227, 0.3)'
                        }}
                        onMouseOut={(e) => {
                            e.currentTarget.style.transform = 'translateY(0)'
                            e.currentTarget.style.boxShadow = 'none'
                        }}
                    >
                        Continue to Payment
                    </Button>
                </Form>
            </div>
        </FormContainer>
    )
}

export default ShippingScreen
